import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, NavOptions } from 'ionic-angular';
import { CategoryPage } from '../category';
import { ServerUtil } from '../../../providers/server-util/serverUtil';

import { environment as ENV } from "../../../environments/environment" ;
import { Http , Headers, RequestOptions} from '@angular/http';
import { Category } from '../../../pojo/category';



@IonicPage()
@Component({
  selector: 'page-maincategory',
  templateUrl: 'maincategory.html',
})
export class MaincategoryPage {
  
  constructor(public http: Http,public navCtrl: NavController, public navParams: NavParams,public server:ServerUtil) {
    this.initializeItems();
  }
  log(item){
    MaincategoryPage.main_option =item;
    MaincategoryPage.is_main_selected =true;

    this.navCtrl.push(CategoryPage);
  }
  back(){
    this.navCtrl.pop();
  }
  getParentCategory(){
    var link = ENV.BASE_URL+ ENV.CATEGORY_API+ENV.PARENT_CATEGORY_PARAMS;
   
   this.http.get(link,ServerUtil.getHeaders())
    .subscribe(d => {
      this.data.response = d["_body"]; 
      console.log(this.data.response);
      let data_array =JSON.stringify(d.json());
      let cat = JSON.parse(data_array);
      this.category_data = cat.data;
     
    }, error => {
      console.log("Oooops!");
    });
     }
  initializeItems() {
    this.getParentCategory();  
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad MaincategoryPage');
  }
  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    const val = ev.target.value;

    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item) => {
        return (item.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }
}



